.chat_page {
    width: 100%;
    height: calc(100vh - 190px);
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    background-color: #eee;
}

.chat {
    width: 50%;
    /*padding: 10px;*/
    margin-top: 30px;
    -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
            box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
    /*overflow: hidden;*/
    border-radius: 5px
}

@media screen and (max-width: 776px) {
    .chat {
        width: 80%;
    }
}

.chat .chat__section {
    width: 100%;
    height: 400px;
    overflow-y: scroll;
    background-color: #fff;
    border-radius: 5px 5px 0 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
}

.chat .chat__messages {
    width: 95%;
    margin: 0;
    padding: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 10px;
}

.chat__messages ul {
    overflow-y: scroll;
}

.chat__messages li {
    list-style: none;
    padding: 5px 10px;
    border-radius: 20px;
    background-color: #666666;
    color: #ffffff;
    margin: 5px 0;
    position: relative;
}
.chat__messages li:first-child {
    margin-top: 0;
}

.chat__messages li:last-child {
    margin-bottom: 0;
}

.chat__messages .to_left {
    -ms-flex-item-align: start;
        align-self: flex-start;
    text-align: left;
}

.chat__messages .to_left:before {
    content: "";
    width: 0;
    height: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-right: 5px solid #666666;
    position: absolute;
    left: -4px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}

.chat__messages .to_right {
    -ms-flex-item-align: end;
        align-self: flex-end;
    text-align: right;
    background-color: #80A1FA;
}

.chat__messages .to_right:before {
    content: "";
    width: 0;
    height: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid #80A1FA;
    position: absolute;
    right: -4px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
}



.chat__form form {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.chat__form input {
    -ms-flex-preferred-size: 1;
        flex-basis: 1;
    -ms-flex-positive: 3;
        flex-grow: 3;
    border: 1px solid #80A1FA;
    background: none;
    padding: 10px 20px;
    outline: none;
    border-radius: 0 0 0 5px;
}

.chat__form button {
    -ms-flex-preferred-size: 1;
        flex-basis: 1;
    -ms-flex-positive: 1;
        flex-grow: 1;
    /*padding: 11px 20px;*/
    background-color: #80A1FA;
    color: #ffffff;
    border: none;
    -webkit-transition: 0.4s ease-in-out;
    -o-transition: 0.4s ease-in-out;
    transition: 0.4s ease-in-out;
    cursor: pointer;
    border-radius: 0 0 5px 0;
}

.chat__form button:hover {
    background-color: #9900FF;
}


