.login_page {
    width: 100%;
    height: calc(100vh - 190px);
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: start;
        align-items: flex-start;
    background-color: #eee;
}

.login {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 40%;
    margin-top: 30px;
}

.login__form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    background-color: #ffffff;
    margin: 0;
    padding: 40px 0;
}

.login__form input {
    border: 1px solid #80A1FA;
    background: none;
    padding: 10px 20px;
    margin: 10px 0 10px 10px;
    outline: none;
}
.login__form input:focus {
    -webkit-box-shadow: 0 0 5px #80A1FA;
            box-shadow: 0 0 5px #80A1FA;
}

.login__form button {
    padding: 11px 20px;
    background-color: #80A1FA;
    color: #ffffff;
    border: none;
    -webkit-transition: 0.4s ease-in-out;
    -o-transition: 0.4s ease-in-out;
    transition: 0.4s ease-in-out;
    cursor: pointer;
}

.login__form button:hover {
    background-color: #9900FF;
}

.login__image-presenter {
    width: 100%;
    height: 300px;
    overflow: hidden;
    margin: 0;
    /*background-color: red;*/
}

.login__image-presenter > img {
    -o-object-fit: cover;
       object-fit: cover;
    max-height: 100%;
    height: 100%;
    min-width: 100%;
    /*display: table-cell;*/
}


